/*
 In real app, would typically use Sass, Less, or PostCSS with cssnext to centralize variables
 like the error color of red in a separate single file.
 */
.textinput {
  margin-bottom: 16px;
}

.textinput__error {
  color: red;
}

.textinput__input--state-error {
  border: solid 1px red;
}
/* Note how short the selectors can be here compared to plain CSS. With CSS modules, everything
is scoped to the component automatically, so you don't have to spend time
writing long, specific CSS selectors like we do with BEM. - they'll only apply
to the component where this css is imported. :)
*/
.textInput_error__3Weri {
  color: red;
}

.textInput_inputError__2zM_V {
  border: solid 1px red;
}

.textInput_fieldset__2FUpQ {
  margin-bottom: 16px;
}

/* Global styles */
body {
  font-family: Arial, Helvetica, sans-serif;
  color: #666666;
}

a, a:visited {
  color: #666666;
}

.clear {
  clear: both;
}

td {
  border-top: solid 1px #d3d3d3;
}

td, th {
  padding: 10px;
}

table {
  border-collapse:collapse;
}

/* Documentation site component styles */
.example {
  border: solid 1px #d3d3d3;
  padding: 16px;
  margin-bottom: 16px;
  background-color: #f4f6f9;
}

.example h4:first-of-type {
   margin-top: 0;
}

.example p:last-of-type {
   margin-bottom: 0;
}

.props tr td:last-of-type {
  text-align: center;
}

.props th, td {
  text-align: left;
  padding: 5px;
}

.navigation {
  float: left;
  width: 250px;
  list-style-type: none;
  padding: 0;
}

.componentpage {
  margin-left: 275px;
}
